::-webkit-input-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
::-moz-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
:-moz-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
:-ms-input-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
:focus {
    &::-webkit-input-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &::-moz-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &:-moz-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &:-ms-input-placeholder {
        opacity: 0;
        transition: opacity 0.3s ease;
    }
}
// btn
.b-link{
    padding: 0.667em 0 0.500em 1.250em;
    display: inline-block;
    vertical-align: top;
    border-bottom: 0.167em solid $color-greyD;
    background: none;
    font-size: 0.857em;
    font-weight: bold;
    line-height: 1.167em;
    letter-spacing: 0.083em;
    text-transform: uppercase;
    color: $color-greyD;
    text-decoration: none;
    transition: all 0.3s ease;
    position: relative;
    cursor: pointer;
    &:before{
        width: 0;
        height: 0;
        margin-top: -0.417em;
        display: block;
        content: '';
        @include triangle(to right, 0.750em, 0.833em, $color-greyD)
        position: absolute;
        top: 50%;
        left: 0;
    }
    &:hover{
        border-bottom-color: $color-green;
        color: $color-green;
        &:before{
            @include triangle(to right, 0.750em, 0.833em, $color-green)
        }
    }
}
.b-btn-close{
    width: 2.857em;
    height: 2.857em;
    padding: 1.071em;
    display: block;
    border: none;
    background: none;
    font-size: 1.000em;
    text-indent: -9999em;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    &:before,
    &:after{
        width: 0.714em;
        height: 0.143em;
        margin: -0.071em 0 0 -0.357em;
        display: block;
        content: '';
        background: $color-greyD;
        transition: all 0.3s ease;
        position: absolute;
        top: 50%;
        left: 50%;
    }
    &:before{
        transform: rotate(-45deg);
    }
    &:after{
        transform: rotate(45deg);
    }
    &:hover{
        &:before,
        &:after{
            background: $color-green;
        }
    }
    &--lg{
        width: 3.286em;
        height: 3.286em;
        &:before,
        &:after{
            width: 1.143em;
            margin-left: -0.571em;
            background: $color-body;
        }
    }
}
.b-btn{
    min-width: 16.000em;
    height: 4.500em;
    padding: 1.500em;
    display: inline-block;
    border: 0.100em solid $color-body;
    font-size: 0.714em;
    font-weight: bold;
    line-height: 1.300em;
    text-align: center;
    letter-spacing: 0.200em;
    text-transform: uppercase;
    color: $color-body;
    text-decoration: none;
    transition: all 0.3s ease;
    position: relative;
    cursor: pointer;
    &:hover{
        border-color: $color-green;
        background: $color-green;
        color: $color-greyL;
        text-decoration: none;
    }
}
// jq-number
.jq-number {
    max-width: 5.143em;
    padding: 0 1.143em;
    display: block;
    font-size: 1.000em;
    font-weight: bold;
    line-height: 1.143em;
    position: relative;
    &__field {
        width: 100%;
        input {
            width: 100%;
            height: 1.143em;
            padding: 0 0.357em;
            border: none;
            background: none;
            font-size: 1.000em;
            font-weight: bold;
            line-height: 1.143em;
            text-align: center;
            color: $color-body;
            -moz-appearance: textfield;
            &::-webkit-inner-spin-button,
            &::-webkit-outer-spin-button {
                margin: 0;
                -webkit-appearance: none;
            }
        }
    }
    &__spin {
        width: 1.143em;
        height: 1.143em;
        display: block;
        transition: all 0.3s ease;
        text-align: center;
        color: $color-body;
        position: absolute;
        top: 0;
        cursor: pointer;
        z-index: 99;
        &:before {
            display: block;
            position: relative;
        }
        &.plus {
            right: 0;
            &:before {
                content: '+';
            }
        }
        &.minus {
            left: 0;
            &:before {
                content: '-';
            }
        }
        &:hover {
            color: $color-green;
        }
    }
    &--lg{
        max-width: 8.000em;
    }
}
// input
.b-input{
    width: 100%;
    height: 3.750em;
    padding: 0.833em 1.667em;
    display: block;
    border: 0.083em solid $color-bd;
    background: $color-white;
    font-size: 0.857em;
    font-weight: 300;
    letter-spacing: 0.167em;
    line-height: 1.917em;
    color: $color-body;
    position: relative;
    &--date{
        padding-right: 2.917em;
        background-image: url(../images/svg/date-icon.svg);
        background-position: calc(100% - 1.667em) center;
        background-repeat: no-repeat;
        background-size: 0.917em 0.833em;
    }
}
// radio 
.jq-radio {
    width: 20.000em;
    height: 20.000em;
    margin: 2.000em 15.000em 0px 0px;
    display: inline-block;
    border: 2.000em solid $color-bd;
    border-radius: 50%;
    cursor: pointer;
    font-size: 0.083em;
    line-height: 1.000em;
    transition: all 0.3s ease;
    position: relative;
    &__div{
        display: none;
    }
    &:before{
        width: 10.000em;
        height: 10.000em;
        margin: -5.000em 0 0 -5.000em;
        display: block;
        background: $color-white;
        border-radius: 50%;
        transition: all 0.3s ease;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
    }
    &.checked {
        border-color: $color-green;
        background: $color-green;
        &:before {
            opacity: 1;
        }
    }
}
// checkbox 
.jq-checkbox {
    width: 20.000em;
    height: 20.000em;
    margin: 2.000em 15.000em 0px 0px;
    display: inline-block;
    border: 2.000em solid $color-bd;
    cursor: pointer;
    font-size: 0.083em;
    line-height: 1.000em;
    transition: all 0.3s ease;
    position: relative;
    &__div{
        display: none;
    }
    &:before{
        width: 100%;
        height: 1.000em;
        margin: -0.500em 0 0;
        display: block;
        background-color: transparent;
        transition: all 0.3s ease;
        opacity: 0;
        font-size: 10.000em;
        line-height: 1.000em;
        text-align: center;
        position: absolute;
        top: 50%;
        left: 0;
        @include icomoon("\e906", $color-white);
    }
    &.checked {
        border-color: $color-green;
        background: $color-green;
        &:before {
            opacity: 1;
        }
    }
}
.jq-checkbox,
.jq-radio {
    input{
        position: absolute;
        z-index: -1;
        margin: 0;
        padding: 0;
        opacity: 0;
    }
}
.b-checkbox,
.b-radio{
    padding: 0.417em 0 0 2.500em;
    display: block;
    font-size: 0.857em;
    font-weight: 300;
    line-height: 1.167em;
    color: $color-body;
    position: relative;
    cursor: pointer;
    .jq-checkbox,
    .jq-radio{
        margin: 0;
        position: absolute;
        top: 1.000em;
        left: 0;
    }
    .icon{
        width: 1.333em;
        height: 1.333em;
        margin: -0.500em 0.833em 0 1.333em;
        display: inline-block;
        vertical-align: middle;
        position: relative;
        svg{
            width: 100%;
            height: 100%;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
    &__info{
        padding-left: 1.714em;
        font-size: 1.167em;
        line-height: 1.000em;
        position: relative;
        cursor: pointer;
        &:before{
            width: 1.000em;
            height: 1.000em;
            margin-top: -0.500em;
            display: block;
            font-size: 1.000em;
            line-height: 1.000em;
            overflow: hidden;
            position: absolute;
            top: 50%;
            left: 0;
            @include icomoon("\e905", $color-greyD);
            z-index: 1;
        }
        &:hover{
            &:before{
                color: $color-green;
            }
        }
    }
    &.checked,
    &:hover{
        .b-checkbox__info{
            &:before{
                color: $color-green;
            }
        }
    }
    &--ver2{
        min-width: 8.583em;
        height: 2.083em;
        padding: 0 2.167em 0 0 0.833em;
        border: 0.083em solid $color-bd;
        text-align: center;
        line-height: 1.917em;
        transition: all 0.3s ease;
        .jq-checkbox{
            width: 26.000em;
            height: 100%;
            border: none;
            border-left: 1.000em solid $color-bd;
            position: absolute;
            top: 0;
            left: auto;
            right: 0;
            &:before{
                width: 100%;
                height: 1.000em;
                margin: -0.571em 0 0;
                display: block;
                content: '+';
                opacity: 1;
                font-family: $ff-helvetica;
                font-size: 14.000em;
                font-weight: bold;
                line-height: 1.000em;
                text-align: center;
                color: $color-bd;
                position: absolute;
                top: 50%;
                left: 0;
            }
        }
        &.checked{
            border-color: $color-green;
            background: $color-green;
            color: $color-white;
            .jq-checkbox{
                border-color: rgba(52, 52, 52, 0.2);
                &:before{
                    margin-top: -0.714em;
                    content: '-';
                    color: $color-white;
                }
            }
        }
    }
    &--ver3{
        width: 100%;
        padding: 0.417em 3.833em 0 0;
        @include flex(to space-between);
        .jq-checkbox{
            width: 26.000em;
            height: 26.000em;
            left: auto;
            right: 0;
            &:before{
                width: 100%;
                height: 1.000em;
                margin: -0.571em 0 0;
                display: block;
                content: '+';
                opacity: 1;
                font-family: $ff-helvetica;
                font-size: 14.000em;
                font-weight: bold;
                line-height: 1.000em;
                text-align: center;
                color: $color-bd;
                position: absolute;
                top: 50%;
                left: 0;
            }
        }
        &:hover{
            color: $color-green;
        }
        &.checked{
            color: $color-green;
            .jq-checkbox{
                &:before{
                    margin-top: -0.714em;
                    content: '-';
                    color: $color-white;
                }
            }
        }
    }
}
.is-os-mac{
    .b-checkbox,
    .b-radio{
        &__info{
            &:before{
                margin-top: -0.700em;
            }
        }
    }
}
// select
.b-select {
    width: 100%;
    font-size: 0.857em;
    font-weight: 300;
    line-height: 1.167em;
    border: 0.083em solid $color-bd;
    color: $color-black;
    vertical-align: top;
    cursor: pointer;
    position: relative;
    select{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        opacity: 0;
    }
    .jq-selectbox__select {
        height: 3.583em;
        padding: 0.833em 4.167em 0.833em 1.667em;
        line-height: 1.917em;
        position: relative;
        z-index: 11;
    }
    .jq-selectbox__select-text {
        width: 100% !important;
        height: 100%;
        position: relative;
        overflow: hidden;
    }
    .jq-selectbox__trigger {
        width: 4.167em;
        height: 100%;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        &-arrow {
            width: 0;
            height: 0;
            margin: -0.250em 0 0 -0.333em;
            position: absolute;
            top: 50%;
            left: 50%;
            @include triangle(to bottom, 0.667em, 0.500em, $color-body);
        }
    }
    .jq-selectbox__dropdown {
        width: calc(100% + 0.167em);
        padding: 4.417em 0 0.500em;
        border: none;
        background: $color-greyL;
        white-space: normal;
        position: absolute;
        top: -0.083em;
        left: -0.083em;
        z-index: 10;
        ul {
            padding: 0;
            li {
                padding: 0 1.667em 0.833em;
                &.selected,
                &:hover {
                    color: $color-green;
                }
                &.disabled{
                    opacity: 0.5;
                    color: $color-greyD;
                    cursor: default;
                    &.selected,
                    &:hover {
                        color: $color-greyD;
                    }
                }
            }
        }
    }
    .scroll-wrapper {
        .scroll-content{
            li{
                padding-right: 2.083em;
            }
        }
        .scroll-element {
            &.scroll-y {
                bottom: 1.083em;
                right: 1.667em;
            }
        }
    }
    &.dropup {
        .jq-selectbox__dropdown {
            padding: 0.500em 0 4.417em;
            top: auto;
            bottom: -0.083em;
            ul{
                li{
                    padding-top: 0.833em;
                    padding-bottom: 0;
                }
            }
        }
        .scroll-wrapper {
            .scroll-element {
                &.scroll-y {
                    top: 1.083em;
                    bottom: 0;
                }
            }
        }
    }
    &.opened {
        .jq-selectbox__dropdown{
            box-shadow: 0px 0px 1.250em rgba(52, 52, 52, 0.15);
        }
        .jq-selectbox__trigger{
           &-arrow {
                transform: rotate(-180deg);
            }
        }
    }
    &--tm-sort {
        border: none;
        background: $color-greyL;
        .jq-selectbox__select {
            height: 4.167em;
            padding: 1.250em 4.167em 1.250em 1.667em;
            line-height: 1.667em;
        }
        .jq-selectbox__dropdown {
            ul {
                li {
                    padding-bottom: 1.333em;
                    &:after {
                        margin: -0.250em 0 0 1.250em;
                        display: inline-block;
                        vertical-align: middle;
                        font-size: 0.667em;
                        line-height: 1.000em;
                        position: relative;
                        @include icomoon("\e902", $color-body);
                        z-index: 1;
                    }
                    &.icon--down {
                        &:after {
                            margin-top: 0;
                            transform: rotate(180deg);
                        }
                    }
                    &.selected,
                    &:hover {
                        &:after {
                            color: $color-green;
                        }
                    }
                }
            }
        }
        &.dropup {
            .jq-selectbox__dropdown{
                ul{
                    li{
                        padding-top: 1.333em;
                        padding-bottom: 0;
                    }
                }
            }
        }
        &.icon {
            .jq-selectbox__select-text {
                &:after {
                    margin: -0.250em 0 0 1.250em;
                    display: inline-block;
                    vertical-align: middle;
                    font-size: 0.667em;
                    line-height: 1.000em;
                    position: relative;
                    @include icomoon("\e902", $color-body);
                    z-index: 1;
                }
            }
            &.icon--down {
                .jq-selectbox__select-text {
                    &:after {
                        margin-top: 0;
                        transform: rotate(180deg);
                    }
                }
            }
        }
    }
}
// label
.b-lb{
    display: block;
    font-size: 1.000em;
    font-weight: bold;
    line-height: 1.286em;
    color: $color-body;
}
// fild
.b-fild{
    width: 100%;
    padding: 0 0 1.429em;
    position: relative;
    &__col-2{
        width: calc(50% - 0.714em);
    }
    &--row{
        @include flex(to space-between);
    }
}

