@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
.b-page{
	position: relative;
    z-index: 10;
    &:before{
    	width: 1px;
		height: 100vh;
		display: block;
		content: '';
		background: $color-body;
		transition: all 0.3s ease;
		opacity: 0.2;
		position: fixed;
		top: 0;
		right: 0;
		z-index: 11;
	}
    &__nav-main{
    	width: 325px;
		height: 100vh;
		padding: 100px 0 0;
		transition: all 0.3s ease;
    	position: fixed;
    	top: 0;
    	left: -400px;
    	z-index: 8;
    }
    &__nav-cat{
		height: 100vh;
		padding: 100px 0 0;
		transition: all 0.3s ease;
    	position: fixed;
    	top: 0;
    	left: 0;
    	z-index: 7;
    }
    &__header{
    	width: 100%;
    	position: fixed;
    	top: 0;
    	left: 0;
		z-index: 10;
    }
    &__main{
    	min-height: calc(100vh - 198px);
    	padding-top: 100px;
		position: relative;
		z-index: 5;
    }
    &__footer{
    	min-height: 198px;
    }
    &.is-open{
    	&:before{
    		width: calc(100% - 325px);
    		opacity: 0.2;
    	}
    	.b-page__nav-main{
    		left: 0;
    	}
    }
}
// NAV MAIN
.b-main-nav{
	background: $color-greyL;
	font-size: 12px;
	line-height: 14px;
	letter-spacing: 2px;
	text-transform: uppercase;
	@include flex(to middle-col);
	a{
		color: $color-body;
		text-decoration: none;
		&:hover{
			color: $color-green;
			text-decoration: none;
		}
	}
	&__item{
		padding: 27px 50px 27px 70px;
		&.is-active{
			a{
				color: $color-green;
			}
		}
	}
}
// NAV CAT
.b-cat-nav{
	width: 166px;
	background: $color-greyL;
	border-right: 1px solid $color-bd;
	font-size: 12px;
	line-height: 14px;
	letter-spacing: 2px;
	text-transform: uppercase;
	transition: width 0.3s ease;
	@include flex(to middle);
	&:before{
    	width: 1px;
		height: 100%;
		display: block;
		content: '';
		background: $color-body;
		transition: all 0.3s ease;
		opacity: 0;
		position: fixed;
		top: 0;
		left: 165px;
		z-index: 11;
	}
	&__list{
		width: 100%;
		overflow: hidden;
		position: relative;
		z-index: 12;
	}
	.icon{
		width: 40px;
		height: 30px;
		margin-top: -15px;
		position: absolute;
		top: 50%;
		left: 0;
		&:before,
		&:after{
			margin: 0 auto;
			display: block;
			content: '';
			background-position: 0 0;
			background-repeat: no-repeat;
			background-size: 100% 100%;
			transition: all 0.3s ease;
			position: absolute;
			top: 50%;
			left: 50%;
		}
		&:after{
			opacity: 0;
		}
		&--roll{
			&:before,
			&:after{
				width: 26px;
				height: 29px;
				margin: -17px 0 0 -13px;
				background-image: url(../images/svg/roll.svg);

			}
			&:after{
				background-image: url(../images/svg/roll-h.svg);
			}
		}
		&--sushi{
			&:before,
			&:after{
				width: 32px;
				height: 29px;
				margin: -14px 0 0 -16px;
				background-image: url(../images/svg/sushi.svg);

			}
			&:after{
				background-image: url(../images/svg/sushi-h.svg);
			}
		}
		&--set{
			&:before,
			&:after{
				width: 37px;
				height: 25px;
				margin: -13px 0 0 -19px;
				background-image: url(../images/svg/set.svg);

			}
			&:after{
				background-image: url(../images/svg/set-h.svg);
			}
		}
		&--cake{
			&:before,
			&:after{
				width: 34px;
				height: 29px;
				margin: -16px 0 0 -17px;
				background-image: url(../images/svg/cake.svg);

			}
			&:after{
				background-image: url(../images/svg/cake-h.svg);
			}
		}
		&--drink{
			&:before,
			&:after{
				width: 22px;
				height: 42px;
				margin: -28px 0 0 -11px;
				background-image: url(../images/svg/drink.svg);

			}
			&:after{
				background-image: url(../images/svg/drink-h.svg);
			}
		}
	}
	a{
		padding: 17px 0 17px 98px;
		display: block;
		white-space: nowrap;
		color: $color-body;
		text-decoration: none;
		position: relative;
		&:hover{
			color: $color-green;
			text-decoration: none;
			.icon{
				&:after{
					opacity: 1;
				}
			}
		}
	}
	&__item{
		padding: 10px 10px 10px 68px;
		&.is-active{
			a{
				color: $color-green;
				.icon{
					&:after{
						opacity: 1;
					}
				}
			}
		}
	}
	&.is-hover{
		width: 325px;
		&:before{
    		width: 100vw;
    		opacity: 0.2;
    		left: 325px;
    	}	
	}
}
// HEADER
.b-header{
	padding-left: 70px;
	padding-right: 50px;
	border-bottom: 1px solid $color-bd;
	background: $color-greyL;
	@include flex(to top);
	&:before{
		width: 50px;
		height: 100%;
		display: block;
		content: '';
		background: $color-green;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 1;
	}
	&__col{
		height: 100px;
		@include flex(to middle-col);
		position: relative;
		z-index: 3;
		&--logo{
			width: 140px;
		}
		&--btn{
			width: calc(100% - 140px - (50% - 73px));
		}
		&--address{
			width: calc(((50% - 73px) - 209px) / 2);
		}
		&--cart{
			width: calc(((50% - 73px) - 209px) / 2);
			background: $color-green;
			@include flex(to center-col);
		}
		&--phone{
			width: 209px;
			background: $color-green;
			@include flex(to top);
		}
	}
}
.b-logo{
	width: 95px;
	height: 20px;
	position: relative;
	a{
		width: 100%;
		height: 100%;
		text-indent: -9999em;
		overflow: hidden;
		position: absolute;
		top: 0;
		left: 0;
	}
	svg{
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
	}
}
.b-btn-menu{
	width: 55px;
	height: 50px;
	padding: 20px;
	display: block;
	text-indent: -9999em;
	overflow: hidden;
	position: relative;
	cursor: pointer;
	span{
		height: 2px;
		margin: -1px 0 0;
		background: $color-body;
		display: block;
		position: absolute;
		top: 50%;
		left: 20px;
		right: 20px;
		z-index: 2;
		&:before,
		&:after{
			width: 100%;
			height: 2px;
			display: block;
			content: '';
			background: $color-body;
			transition: all 0.3s ease;
			position: absolute;
			left: 0;
		}
		&:before{
			top: -5px;
		}
		&:after{
			bottom: -5px;
		}
	}
	&:hover{
		span{
			background: $color-green;
			&:after,
			&:before{
				background: $color-green;
			}
		}
	}
	&.is-open{
		span{
			background-color: transparent;
			&:after,
			&:before{
				top: 0;
				bottom: auto;
			}
			&:before{
				transform: rotate(-45deg);
			}
			&:after{
				transform: rotate(45deg);
			}
		}
		&:hover{
			span{
				background: transparent;
			}
		}
	}
}
.b-btn-address{
	padding-left: 20px;
	display: block;
	font-size: 11px;
	line-height: 16px;
	letter-spacing: 2px;
	text-transform: uppercase;
	color: $color-body;
	text-decoration: none;
	cursor: pointer;
	position: relative;
	&:before{
		margin-top: -6px;
		display: block;
		font-size: 12px;
		line-height: 12px;
		position: absolute;
		top: 50%;
		left: 0;
		@include icomoon("\e900", $color-green);
	}
}
.b-btn-cart{
	padding-left: 24px;
	display: block;
	border: none;
	background: none;
	font-size: 12px;
	font-weight: bold;
	line-height: 16px;
	letter-spacing: 2px;
	text-transform: uppercase;
	color: $color-greyL;
	text-decoration: none;
	position: relative;
	cursor: pointer;
	&:before{
		margin-top: -7px;
		display: block;
		font-size: 14px;
		line-height: 14px;
		position: absolute;
		top: 50%;
		left: 0;
		@include icomoon("\e903", $color-greyL);
		z-index: 1;
	}
	span{
		width: 12px;
		height: 12px;
		padding-top: 1px;
		display: block;
		background: $color-body;
		border-radius: 50%;
		font-size: 8px;
		font-weight: 300;
		line-height: 8px;
		letter-spacing: 0;
		text-align: center;
		position: absolute;
		top: 0;
		left: 9px;
		z-index: 2;
	}
	&--sm{
		width: 16px;
	    height: 16px;
	    padding: 0;
	    &:before{
	        color: $color-body;
	    }
	    &:hover{
	        &:before{
	        	color: $color-green;
	        }
	    }
	}
}
.b-list-phones{
	width: 100%;
	height: 100%;
	padding-left: 16px;
	padding-right: 10px;
	transition: all 0.3s ease;
	position: absolute;
	top: 0;
	left: 0;
	@include flex(to middle-col);
	&__numb{
		padding: 12px 0;
		position: relative;
		z-index: 10;
	}
	&__sub{
		width: 100%;
		padding: calc(100px + 44px + 16px + 12px) 10px 32px 16px;
		display: none;
		transition: all 0.3s ease;
		position: absolute;
		top: -100px;
		left: 0;
		z-index: 1;
	}
	&:hover{
		.b-list-phones__sub{
			display: block;
			background: $color-green;
			box-shadow: 0px 0px 15px rgba(52, 52, 52, 0.15);
		}
	}
}
.b-phones-mob{
	display: none;
}
.b-phone-numb{
	padding-left: 24px;
	display: block;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	letter-spacing: 2px;
	text-transform: uppercase;
	color: $color-greyL;
	text-decoration: none;
	position: relative;
	cursor: pointer;
	&:before{
		margin-top: -6px;
		display: block;
		font-size: 12px;
		line-height: 12px;
		position: absolute;
		top: 50%;
		left: 0;
		@include icomoon("\e90b", $color-greyL);
		z-index: 1;
	}
}

// FOOTER
.b-footer{
	padding: 44px 70px 44px calc(166px + 65px);
	background: $color-greyL;
	border-top: 1px solid $color-bd;
	font-size: 12px;
	font-weight: 300;
	line-height: 16px;
	letter-spacing: 2px;
	color: $color-greyD;
	.b-logo{
		margin-bottom: 48px;
	}
	.b-share-nav{
		width: 307px;
	}
	&__left{
		width: calc(100% - 438px);
		padding: 8px 0 0;
	}
	&__right{
		width: 438px;
		order: 2;
		@include flex(to right-col);
	}
	&__row{
		width: 100%;
		padding: 0 0 43px;
		line-height: 24px;
		text-transform: uppercase;
		text-align: right;
		@include flex(to top);
		&-col{
			width: 50%;
		}
	}
}
.b-share-nav{
	font-size: 13px;
	@include flex(to middle);
	@include flex(to space-between);
	&__tit{
		font-size: 12px;
		font-weight: 300;
		line-height: 16px;
	}
	&__list{
		@include flex(to right);
	}
	&__item{
		padding-left: 12px;
	}
	a{
		color: $color-greyD;
		text-decoration: none;
		&:hover{
			color: $color-green;
			text-decoration: none;
		}
	}
}
// MAIN
.b-main{
	padding-left: 166px;
	overflow: hidden;
	&__center{
		padding: 100px 60px 0;
		position: relative;
	}
}
// home page
// -- slider
.b-home-slider{
	margin-bottom: 33px;
	background: $color-greyL;
	position: relative;
	.slick-dots{
		width: 50%;
		padding-left: 20px;
		left: auto;
		right: 0;
		text-align: left;
	}
}
.b-home-slid{
	position: relative;
	@include flex(to top);
	&__col{
		width: 50%;
		height: 550px;
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
		position: relative;
		@include flex(to middle-col);
		&--img{
			order: 2;
		}
	}
	&__entry{
		padding: 0 60px;
	}
	&__sub-tit{
		padding: 27px 0 27px 16px;
		font-size: 12px;
		font-weight: bold;
		line-height: 16px;
		letter-spacing: 1px;
		text-transform: uppercase;
		position: relative;
		&:before{
			width: 2px;
			height: 20px;
			margin-top: -10px;
			display: block;
			content: '';
			background: $color-green;
			position: absolute;
			top: 50%;
			left: 0;
		}
	}
	&__tit{
		padding: 27px 0;
		font-size: 64px;
		font-weight: bold;
		line-height: 72px;
		letter-spacing: 3px;
		color: $color-body;
	}
	&__btn{
		padding: 32px 0;
	}
	img{
		width: 100%;
		display: block;
	}
}
// --txt home
.b-txt-page{
	padding: 0 0 107px;
	padding: 0 0 70px;
	position: relative;
	.b-tit,
	.b-sub-tit{
		padding-bottom: 7px;
	}
	p{
		padding: 12px 0;
	}
	&__head{
		padding-bottom: 7px;
		position: relative;
		@include flex(to bottom);
		@include flex(to space-between);
		.b-tit,
		.b-sub-tit{
			padding-bottom: 0;
		}
	}
	&__entry{
		max-width: 700px;
	}
	&__row{
		padding-top: 57px;
		position: relative;
		@include flex(to middle);
	}
	&__col{
		&-left{
			width: 33%;
			position: relative;
			img{
				width: 100%;	
				display: block;	
			}
		}
		&-right{
			width: 67%;
			padding-left: 100px;
			position: relative;
		}
	}
}
// catalog
.b-cat{
	padding: 0 0 120px;
	position: relative;
	&__head{
		width: 100%;
		padding: 0 0 22px;
		position: relative;
		@include flex(to middle);
		&-tit{
			width: calc(100% - 111px - 265px);
			padding-right: 20px;
		}
		&-nav{
			width: 111px;
			@include flex(to middle);
		}
		&-btn{
			width: 265px;
			padding-left: 30px;
			@include flex(to right-col);
		}
	}
	&__slider{
		margin: 0 -15px;
		position: relative;
		&-item{
			padding: 15px;
		}
	}
	&__list{
		margin: 0 -15px;
		padding-top: 15px;
		position: relative;
		&-item{
			width: 25%;
			padding: 15px;
			float: left;
			position: relative;
		}
	}
}
.b-cat-post{
	height: 400px;
	padding: 0 20px 35px;
	background: $color-greyL;
	transition: box-shadow 0.3s ease;
	position: relative;
	overflow: hidden;
	&__lb{
		width: 16px;
		height: 16px;
		position: absolute;
		top: 16px;
		right: 16px;
		z-index: 10;
		svg{
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	&__img{
		height: 231px;
		padding-bottom: 20px;
		position: relative;
		overflow: hidden;
		z-index: 2;
		@include flex(to middle-col);
		img{
			max-width: 100%;
			max-height: 100%;
			margin: 0 auto;
			display: block;
		}
	}
	&__tit{
		height: 39px;
		margin-bottom: 10px;
		font-size: 14px;
		font-weight: bold;
		line-height: 18px;
		letter-spacing: 1px;
		color: $color-body;
		position: relative;
		overflow: hidden;
	}
	&__info{
		height: 70px;
		margin-bottom: 15px;
		font-size: 12px;
		font-weight: 300;
		line-height: 18px;
		letter-spacing: 1px;
		position: relative;
		overflow: hidden;
		strong{
			font-weight: bold;
			color: $color-green;
		}
	}
	&__foot{
		width: 100%;
		padding: 0 20px 17px;
		position: absolute;
		bottom: 0;
		left: 0;
		@include flex(to middle);
		@include flex(to space-between);
	}
	&__price{
		font-size: 14px;
		font-weight: bold;
		line-height: 18px;
		letter-spacing: 2px;
		color: $color-body;
	}
	&:hover{
		box-shadow: 0px 0px 15px rgba(52, 52, 52, 0.15);
	}
}
.b-sort{
	padding: 0 0 42px;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	text-align: center;
	position: relative;
	&__item{
		padding: 0 20px;
		display: inline-block;
		vertical-align: top;
		a{
			padding: 7px 0 5px 18px;
			display: block;
			border-bottom: 2px solid transparent;
			vertical-align: top;
			color: $color-greyD;
			text-decoration: none;
			transition: all 0.3s ease;
			position: relative;
			&:before{
		        width: 0;
		        height: 0;
		        margin-top: -5px;
		        display: block;
		        content: '';
		        opacity: 0;
		        transition: all 0.3s ease;
		        @include triangle(to right, 9px, 10px, $color-green)
		        position: absolute;
		        top: 50%;
		        left: 0;
		    }
		}
		&:hover,
		&.is-active,
		&.ui-state-active{
			a{
				color: $color-body;
				text-decoration: none;
				&:before{
		            opacity: 1;
		        }
			}
		}
		&.is-active,
		&.ui-state-active{
			a{
				border-color: $color-green;
			}
		}
	}
}
.b-filter{
	padding: 0 0 91px;
	position: relative;
	@include flex(to top);
	@include flex(to space-between);
	&__left{
		width: calc(68% - 15px);
		height: 50px;
		background: $color-greyL;
		@include flex(to middle);
	}
	&__right{
		width: calc(32% - 15px);
	}
	&__item{
		padding: 0 22px;
		white-space: nowrap;
	}
}
// card
.b-card{
	padding: 0 60px;
	background: $color-greyL;
	position: relative;
	&__entry{
		width: 100%;
		position: relative;
		@include flex(to middle);
	}
	&__lb{
		width: 31px;
		height: 31px;
		position: absolute;
		top: 100px;
		right: 0;
		z-index: 10;
		svg{
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	&__txt{
		width: 548px;
		padding: 71px 0 80px;
	}
	&__img{
		width: calc(100% - 548px);
		padding-left: 45px;
		order: 2;
		img{
			width: 100%;
			display: block;
		}
	}
	.b-tit{
		padding-bottom: 48px;
	}
	&__cal{
		margin: 0 0 26px;
		padding-left: 24px;
		font-size: 14px;
		line-height: 14px;
		color: $color-greyD;
		position: relative;
		cursor: pointer;
		&:before{
			margin-top: -7px;
			display: block;
			font-size: 14px;
			line-height: 14px;
			position: absolute;
			top: 50%;
			left: 0;
			@include icomoon("\e905", $color-greyD);
			z-index: 1;
		}
		&:hover{
			color: $color-green;
			&:before{
				color: $color-green;
			}
		}
	}
	&__info{
		margin: 0 0 45px;
		font-size: 14px;
		font-weight: 300;
		line-height: 18px;
		letter-spacing: 1px;
		color: $color-body;
		position: relative;
		strong{
			font-weight: bold;
			color: $color-green;
		}
	}
	&__sel{
		padding: 0 0 28px;
	}
	&__add{
		width: 100%;
		padding: 0 0 48px;
		font-size: 14px;
		font-weight: bold;
		line-height: 23px;
		color: $color-body;
		position: relative;
		@include flex(to middle);
		&-txt {
			width: 93px;
		}
		&-check{
			width: calc(100% - 93px);
		}
		.b-checkbox{
			width: calc(25% - 10px);
			min-width: 10px;
			margin-left: 10px;
			float: left;
		}
	}
	&__foot{
		width: 100%;
		position: relative;
		@include flex(to middle);
		&-col{
			&--price{
				width: calc(100% - 142px - 205px);
				font-size: 24px;
				font-weight: bold;
				line-height: 26px;
				color: $color-body;
			}
			&--numb{
				width: 142px;
				padding-left: 30px;
			}
			&--btn{
				width: 205px;
				padding-left: 45px;
				.b-btn{
					min-width: 100%;
				}
			}
		}
	}
}
// cart
.b-cart{
	position: relative;
	&__left{
		padding: 0 38% 0 0;
		position: relative;
	}
	&__right{
		width: calc(38% - 50px);
		position: absolute;
		top: 0;
		right: 0;
	}
}
.b-cart-numb{
	font-size: 14px;
	font-weight: bold;
	line-height: 24px;
	color: $color-body;
	position: relative;
}
.b-cart-list{
	padding: 0 0 90px;
	position: relative;
	&__item{
		padding-top: 20px;
		position: relative;
		&:first-child{
			padding-top: 0;
		}
	}
}
.b-cart-post{
	height: 100px;
	padding: 0 15px;
	background: $color-greyL;
	position: relative;
	@include flex(to middle);
	&__img{
		width: 111px;
		margin-right: 17px;
		padding: 0 13px 0 0;
		position: relative;
		img{
			width: 100%;
			display: block;
			position: relative;
			z-index: 1;
		}
	}
	&__txt{
		width: calc(100% - 111px - 17px - 102px - 105px - 46px);
		padding: 0 30px 0 0;
	}
	&__numb{
		width: 102px;
		padding: 0 30px 0 0;
	}
	&__price{
		width: 105px;
		font-size: 14px;
		font-weight: bold;
		line-height: 16px;
		color: $color-body;
	}
	&__btn{
		width: 46px;
	}
	&__lb{
		width: 16px;
		height: 16px;
		position: absolute;
		top: 15px;
		right: 0;
		z-index: 10;
		svg{
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	&__tit{
		padding: 0 0 15px;
		font-size: 14px;
		font-weight: bold;
		line-height: 16px;
		color: $color-body;
	}
	&__info{
		font-size: 12px;
		font-weight: bold;
		line-height: 14px;
		color: $color-green;
	}
}
.b-cart-del{
	position: relative;
	&__form{
		padding: 0 0 44px;
	}
	&__row{
		padding: 0 0 30px;
	}
	&__left{
		width: calc(100% - 470px);
		padding: 0 30px 0 0;
		float: left;
		position: relative;
		&--middle {
			min-height: 40px;
			@include flex(to middle-col);
		}
	}
	&__right{
		width: 470px;
		float: left;
		position: relative;
	}
}
.b-cart-total{
	margin: 44px 0 0;
	padding: 27px 30px;
	background: $color-greyL;
	font-size: 14px;
	font-weight: bold;
	line-height: 18px;
	color: $color-body;
	position: relative;
	@include flex(to middle);
	&__left{
		width: calc(100% - 340px);
		padding: 0 30px 0 0;
		span{
			padding-left: 20px;
		}
	}
	&__right{
		width: 340px;
		position: relative;
		@include flex(to space-between);
		.b-btn{
			min-width: calc(50% - 10px);
		}
	}
}
.b-cart-widget{
	margin: 0 0 20px;
	padding: 20px 30px;
	background: $color-greyL;
	position: relative;
	&__tit{
		margin: 0 0 14px;
		padding: 0 0 18px;
		border-bottom: 1px solid $color-bd;
		font-size: 14px;
		font-weight: bold;
		line-height: 18px;
		color: $color-body;
		position: relative;
	}
	&__head{
		position: relative;
		@include flex(to middle);
		@include flex(to space-between);
		.b-cart-widget__tit{
			margin: 0;
			padding: 0 20px 0 0;
			border: none;
		}
	}
	&__item{
		padding: 9px 0;
	}
}
.b-cart-promo{
	padding: 30px 0 0;
	position: relative;
	@include flex(to top);
	&__fild{
		width: 58%;
		padding: 0 20px 0 0;
	}
	&__btn{
		width: 42%;
	}
	.b-input{
		color: $color-bd;
	}
	.b-btn{
		min-width: 100%;
	}
}
// news
.b-new-main{
	padding: 0 0 107px;
	position: relative;
	&__img{
		padding: 0 0 45px;
		position: relative;
		img{
			width: 100%;
			display: block;
		}
	}
	&__head{
		padding: 0 0 24px;
		position: relative;
		@include flex(to bottom);
		@include flex(to space-between);
	}
	p{
		padding: 0 0 24px;
	}
}
.b-news-list{
	margin: 0 -15px;
	padding: 0 0 90px;
	position: relative;
	&__item{
		width: 25%;
		padding: 0 15px 40px;
		float: left;
		&:nth-child(4n+1){
			clear: both;
		}
	}
}
.b-new-prev{
	&__img{
		padding: 0 0 29px;
		img{
			width: 100%;
			display: block;
		}
	}
	&__tit{
		padding: 0 0 15px;
		font-size: 18px;
		font-weight: bold;
		line-height: 20px;
		color: $color-body;
		a{
			color: $color-body;
			text-decoration: none;
			&:hover{
				color: $color-green;
				text-decoration: none;
			}
		}
	}
	&__foot{
		padding: 12px 0 0;
		position: relative;
		@include flex(to bottom);
		@include flex(to space-between);
	}
}
// def page
.b-post-page{
	padding: 0 0 110px;
	position: relative;
	&__row{
		margin: 0 -25px;
		position: relative;
	}
	&__col{
		padding: 0 25px 7px;
		float: left;
		&--lg{
			width: 64%;
		}
		&--md{
			width: 50%;
		}
		&--sm{
			width: 36%;
		}
		img{
			max-width: 100%;
			margin-top: 10px;
			margin-bottom: 10px;
		}
	}
	h1,
	h2,
	h3,
	h4,
	h5,
	h6{
		padding: 0 0 28px;
		font-weight: bold;
		letter-spacing: 4px;
	}
	h1{
		font-size: 36px;
		line-height: 40px;
	}
	h2{
		font-size: 24px;
		line-height: 30px;
	}
	h3{
		font-size: 24px;
		line-height: 24px;
	}
	h4{
		font-size: 20px;
		line-height: 20px;
	}
	h5{
		font-size: 18px;
		line-height: 18px;
	}
	h6{
		font-size: 14px;
		line-height: 14px;
	}
	hr{
		margin: 22px 0 48px;
		border: none;
		display: block;
		position: relative;
		&:before{
	        display: block;
	        font-size: 16px;
	        line-height: 16px;
	        position: relative;
	        @include icomoon("\e904", $color-green);
	        z-index: 1;
	    }
	}
	p{
		padding: 0 0 24px;
	}
	img{
		max-width: 100%;
		display: block;
		position: relative;
		&.alignleft{
			margin: 0 50px 30px 0;
			float: left;
		}
		&.alignright{
			margin: 0 0 30px 50px;
			float: left;
		}
		&.aligncenter{
			max-width: 100%;
			display: block;
		}
		&.alignnone{
			max-width: 100%;
		}
	}
	.b-tit,
	.b-sub-tit{
		padding: 0 0 41px;
	}
	&__imgs{
		margin: 0 -15px;
		position: relative;
		&-item{
			width: 50%;
			padding: 5px 15px 25px;
			float: left;
		}
		img{
			width: 100%;
			margin: 0;
			display: block;
		}
	}
}
// delivery
.b-del-speed{
	margin: 0 0 140px;
	background-image: url(../images/bg-img01.png);
	background-color: $color-greyL;
	background-position: left bottom;
	background-repeat: no-repeat;
	position: relative;
	@include flex(to space-between);
	&__img{
		width: calc(100% - 750px);
		position: relative;
		.icon{
			width: 123px;
			height: 299px;
			margin: -150px 0 0;
			display: block;
			position: absolute;
			top: 50%;
			left: 25%;
		}
	}
	&__txt{
		width: 750px;
		height: 400px;
		padding: 0 70px;
		position: relative;
		@include flex(to middle-col);
		.icon{
			width: 46px;
			height: 48px;
			margin: 0 auto 31px;
			position: relative;
		}
	}
	&__row{
		margin: 0 -15px;
		position: relative;
	}
	&__col{
		width: 33.33%;
		padding: 40px 15px 0;
		float: left;
		text-align: center;
	}	
	.b-sub-tit{
		padding-bottom: 44px;
	}
	svg{
		width: 100%;
		height: 100%;
		display: block;
		position: absolute;
		top: 0;
		left: 0;
	}
}
.b-del-how{
	padding: 0 0 137px;
	margin: 0 -15px;
	white-space: nowrap;
	position: relative;
	@include flex(to space-between);
	&__col{
		width: 25%;
		padding: 0 15px;
		position: relative;
	}
	&__list{
		margin: 0 -15px;
		position: relative;
		&-item{
			width: 50%;
			padding: 0 15px;
			float: left;
			position: relative;
		}
	}
	.img{
		height: 50px;
		margin: 0 0 19px;
		position: relative;
	}
	.icon{
		position: absolute;
		bottom: 0;
		left: auto;
		&--phone{
			width: 45px;
			height: 47px;
		}
		&--pc{
			width: 45px;
			height: 42px;
		}
		&--phone{
			width: 45px;
			height: 42px;
		}
		&--wallet{
			width: 46px;
			height: 43px;
		}
		&--card{
			width: 45px;
			height: 33px;
		}
		&--courier{
			width: 45px;
			height: 50px;
		}
		&--time{
			width: 47px;
			height: 47px;
		}
		svg{
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	.b-sub-tit{
		padding-bottom: 47px;
	}
}
.b-del-area{
	padding: 0 0 150px;
	position: relative;
	&__txt{
		width: 48%;
		padding: 7px 80px 0 0;
		float: left;
		position: relative;
	}
	&__map{
		width: 52%;
		float: left;
		position: relative;
	}
	.b-sub-tit{
		padding-bottom: 27px;
	}
	img{
		width: 100%;
		display: block;
	}
}
// 1300px
@media (max-width: $lg) {
    @import 'lg';
}
// 1200px
@media (max-width: $md) {
    @import 'md';
}
// 1024px
@media (max-width: $sm) {
    @import 'sm';
}
@media (max-width: 940px) {
    // catalog
	.b-cat,{
		&__list{
			&-item{
				width: 33.33%;
			}
		}
	}
	// news
	.b-news-list,{
		&__item{
			width: 33.33%;
			&:nth-child(4n+1){
				clear: none;
			}
			&:nth-child(3n+1){
				clear: both;
			}
		}
	}
}
@media (max-width: 800px) {
    // catalog
	.b-cat{
		&__list{
			&-item{
				width: 50%;
			}
		}
	}
	// news
	.b-news-list,{
		&__item{
			width: 50%;
			&:nth-child(3n+1){
				clear: none;
			}
			&:nth-child(2n+1){
				clear: both;
			}
		}
	}
}
@media (max-width: 550px) {
    // catalog
	.b-cat{
		&__list{
			&-item{
				width: auto;
				float: none;
			}
		}
	}
	// news
	.b-news-list,{
		&__item{
			width: auto;
			float: none;
		}
	}
}
// 767px
@media (max-width: $xs) {
    @import 'xs';
}
// 414px
@media (max-width: $xxs) {
    @import 'xxs';
}