/* Slider */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}
.slick-dots{
    width: 100%;
    height: 0.571em;
    font-size: 1.000em;
    white-space: nowrap;
    text-align: center;
    position: absolute;
    bottom: 2.143em;
    left: 0;
    @extend .cf;
    li{
        padding: 0 0.714em;
        display: inline-block;
        vertical-align: top;
        position: relative;
        button{
            width: 0.571em;
            height: 0.571em;
            display: block;
            border: none;
            background: $color-body;
            transition: all 0.3s ease;
            font-size: 1.000em;
            text-indent: -9999em;
            overflow: hidden;
            opacity: 0.5;
            position: relative;
            cursor: pointer;
            &:hover{
                background: $color-green;
                opacity: 1;
            }
        }
        &.slick-active{
            button{
                background: $color-green;
                opacity: 1;
            }
        }
    }
}
.slick-arrow{
    width: 1.143em;
    height: 1.143em;
    display: block;
    border: none;
    background: none;
    font-size: 1.000em;
    text-indent: -9999em;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    z-index: 100;
    &:before{
        margin-top: -0.500em;
        display: block;
        font-size: 0.571em;
        line-height: 1.000em;
        text-indent: 0;
        position: absolute;
        top: 50%;
        left: 0;
        @include icomoon("\e901", $color-body);
        z-index: 1;
    }
    &.slick-next{
        &:before{
            transform: rotate(180deg);
            left: auto;
            right: 0;
        }
    }
    &:hover{
        &:before{
            color: $color-green;
        }
    }
    &.slick-disabled{
        display: none !important;
    }
}
.numbers-slides{
    width: 5.714em;
    padding: 0 0.714em;
    font-size: 1.000em;
    font-weight: bold;
    line-height: 1.143em;
    letter-spacing: 0;
    text-align: center;
    .current{
        color: $color-body;
    }
}