// slick.scss
.slick-dots{
    bottom: 20px;
}
.slick-arrow{
	margin-top: -8px;
	position: absolute;
	top: 50%;
	&:before{
		margin-top: -5px;
		font-size: 10px;
		line-height: 10px;
	}
	&.slick-prev{
        left: 0;
    }
	&.slick-next{
        right: 0;
    }
}
// modal.scss
.fancybox-bg {
    background: $color-white;
}
.fancybox-is-open .fancybox-bg {
    opacity: 1;
}
.fancybox-slide--html {
    padding: 0;
}
.fancybox-content{
	padding-left: 0;
	padding-right: 0;
}
.b-modal{
    padding: 0 20px 20px;
    &__head{
        display: block;
        .b-tit,
        .b-sub-tit{
        	padding-bottom: 10px;
        }
    }
    &__entry{
        padding: 0 0 20px;
    }
    &__row{
        &-left{
            width: auto;
            padding: 0 0 15px;
            float: none;
            &--middle {
                min-height: 10px;
                display: block;
            }
        }
        &-right{
            width: auto;
            float: none;
        }
    }
    &__btn{
        padding-top: 10px;
        text-align: center;
    }
}
.fancybox-close-small {
    color: $color-body;
    top: 10px;
    right: 10px;
    &:hover {
        color: $color-green;
    }
}
// form.scss
.b-btn{
    min-width: 140px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 10px;
    letter-spacing: 1px;
}
.b-checkbox,
.b-radio{
	&--ver3{
        padding-right: 32px;
        font-size: 12px;
        line-height: 12px;
        .jq-checkbox{
            width: 20px;
            height: 20px;
        }
    }
}
// fild
.b-fild{
    width: 100%;
    padding: 0 0 20px;
    position: relative;
    &__col-2{
        width: auto;
        padding-bottom: 20px;
    }
    &__col{
    	padding-bottom: 10px;
    }
    &--row{
    	padding-bottom: 0;
        display: block;
    }
}
// base.scss
.b-tit{
    font-size: 26px;
    line-height: 30px;
    letter-spacing: 2px;
}
.b-sub-tit{
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 2px;
}
.sticky-bottom{
    height: 50px;
}
// main.scss
.b-page{
	&:before{
		display: none;
	}
	&__nav-main{
    	width: 100%;
    	padding-top: 70px;
    	left: -1000px;
    }
    &__main{
    	padding-top: 70px;
    }
}
// NAV MAIN
.b-main-nav{
	text-align: center;
}
// NAV CAT
.b-cat-nav{
	font-size: 9px;
	line-height: 10px;
	a{
		padding: 40px 0 0;
	}
	&__item{
		width: 65px;
		padding: 0 5px;
	}
}
// HEADER
.b-header{
	padding-right: 10px;
	&__col{
		height: 70px;
		&--logo{
			width: calc(100% - 35px - 45px - 45px - 45px);
			margin-left: 35px;
		}
		&--btn{
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}
		&--address{
			width: 45px;
		}
		&--cart{
			width: 45px;
		}
		&--phone{
			width: 45px;
		}
	}
}
.b-btn-address{
	width: 25px;
	height: 25px;
	margin: 0 auto;
	padding-left: 0;
	text-indent: -9999em;
	&:before{
		margin: -8px 0 0 -8px;
		font-size: 16px;
		line-height: 16px;
		text-indent: 0;
		left: 50%;
	}
}
.b-btn-cart{
	width: 25px;
	height: 25px;
	margin: 0 auto;
	padding-left: 0;
	text-indent: -9999em;
	&:before{
		margin: -8px 0 0 -8px;
		font-size: 16px;
		line-height: 16px;
		text-indent: 0;
		left: 50%;
	}
	span{
		width: 14px;
		height: 14px;
		padding: 0;
		font-size: 9px;
		line-height: 14px;
		text-indent: 0;
		left: 16px;
	}
	&--sm{
		width: auto;
		height: auto;
		text-indent: 0;
	}
}
.b-list-phones{
	display: none;
}
.b-phones-mob{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	@include flex(to middle-col);
	&__btn{
		width: 15px;
		height: 15px;
		margin: 0 auto;
		text-indent: -9999em;
		position: relative;
		overflow: hidden;
		cursor: pointer;
		&:before{
			margin: -8px 0 0 -8px;
			display: block;
			font-size: 16px;
			line-height: 16px;
			text-indent: 0;
			transition: all 0.3s ease;
			position: absolute;
			top: 50%;
			left: 50%;
			@include icomoon("\e90b", $color-greyL);
			z-index: 1;
		}
		&.is-open{
			&:before,
			&:after{
				width: 100%;
				height: 2px;
				margin: -1px 0 0;
				display: block;
				content: '';
				background: $color-greyL;
				transition: all 0.3s ease;
				position: absolute;
				top: 50%;
				left: 0;
			}
			&:before{
				transform: rotate(-45deg);
			}
			&:after{
				transform: rotate(45deg);
			}
		}
	}
	&__list{
		width: 209px;
		padding: 20px 10px 20px 15px;
		background: $color-green;
		box-shadow: 0px 0px 15px rgba(52, 52, 52, 0.15);
		transition: all 0.3s ease;
		position: absolute;
		top: 100%;
		right: -320px;
	}
	&__item{
		padding: 12px 0;
	}
	&.is-open{
		.b-phones-mob__list{
			right: -10px;
		}
	}
}
// FOOTER
.b-footer{
	padding: 20px;
	text-align: center;
	.b-flex{
		display: block;
	}
	.b-logo{
		margin: 0 auto;
	}
	.b-share-nav{
		width: auto;
		display: block;
	}
	&__left{
		width: auto;
		padding: 10px 0;
	}
	&__right{
		width: auto;
		padding: 10px 0;
		display: block;
	}
	&__row{
		padding: 0;
		font-size: 11px;
		text-align: center;
		&-col{
			width: 50%;
		}
	}
}
.b-share-nav{
	text-align: center;
	display: block;
	&__tit{
		padding: 0 0 10px;
	}
	&__list{
		@include flex(to center);
	}
	&__item{
		padding: 0 10px;
	}
}
// MAIN
.b-main{
	&__center{
		padding-top: 50px;
	}
}
// home page
// -- slider
.b-home-slider{
	.slick-dots{
		width: 100%;
		padding-left: 0;
		text-align: center;
	}
}
.b-home-slid{
	display: block;
	&__col{
		width: auto;
		height: auto;
		display: block;
		&--img{
			height: 250px;
		}
	}
	&__entry{
		padding-top: 40px;
		padding-bottom: 40px;
	}
	&__tit{
		font-size: 40px;
		line-height: 48px;
	}
}
// --txt home
.b-txt-page{
	padding-bottom: 50px;
	p{
		padding: 10px 0;
	}
	&__head{
		padding-bottom: 7px;
		display: block;
		.b-tit,
		.b-sub-tit{
			padding-bottom: 15px;
		}
	}
	&__row{
		padding-top: 30px;
		display: block;
	}
	&__col{
		&-left{
			width: auto;
			padding-bottom: 30px;
		}
		&-right{
			width: auto;
			padding-left: 0;
		}
	}
	&--sm{
		padding-bottom: 50px;
	}
}
// catalog
.b-cat{
	padding: 0 0 50px;
	&__head{
		&-tit{
			width: calc(100% - 160px);
		}
		&-nav{
			display: none;
		}
		&-btn{
			width: 160px;
		}
	}
	&__slider{
		padding: 0 20px;
	}
}
.b-sort{
	&__item{
		margin: 0 8px;
	}
}
.b-filter{
	padding-bottom: 50px;
	display: block;
	&__left{
		width: auto;
		height: auto;
		margin-bottom: 20px;
		padding-top: 10px;
		padding-bottom: 10px;
		display: block;
	}
	&__right{
		width: auto;
	}
	&__item{
		padding-top: 10px;
		padding-bottom: 10px;
	}
}
// card
.b-card{
	&__entry{
		display: block;
	}
	&__lb{
		top: 30px;
	}
	&__txt{
		width: auto;
		padding: 30px 0 50px;
	}
	&__img{
		width: auto;
		padding-top: 30px;
	}
	.b-tit{
		padding-bottom: 30px;
	}
	&__cal{
		margin: 0 0 20px;
	}
	&__info{
		margin: 0 0 30px;
	}
	&__sel{
		padding: 0 0 20px;
	}
	&__add{
		padding: 0 0 30px;
		display: block;
		&-txt {
			width: auto;
			padding-bottom: 10px;
		}
		&-check{
			width: auto;
			margin-left: -10px;
		}
	}
	&__foot{
		padding-bottom: 70px;
		@include flex(to space-between);
		&-col{
			&--price{
				width: calc(100% - 92px);
			}
			&--numb{
				width: 92px;
			}
			&--btn{
				width: 100%;
				padding: 0;
				text-align: center;
				position: absolute;
				bottom: 0;
				left: 0;
				.b-btn{
					min-width: 140px;
				}
			}
		}
	}
}
// cart
.b-cart{
	&__right{
		padding-bottom: 50px;
	}
}
.b-cart-list{
	padding-bottom: 50px;
}
.b-cart-post{
	height: auto;
	padding: 20px;
	display: block;
	&__img{
		width: auto;
		margin-right: 0;
		padding: 0 50px 20px;
		img{
			max-width: 100%;
			width: auto;
			margin: 0 auto;
		}
	}
	&__txt{
		width: auto;
		padding: 0 0 20px;
	}
	&__numb{
		width: auto;
		padding: 0;
		position: absolute;
		bottom: 20px;
		right: 20px;
	}
	&__price{
		width: auto;
		padding-right: 80px;
	}
	&__btn{
		position: absolute;
		top: 0;
		right: 0;
	}
	&__lb{
		top: 10px;
		right: 40px;
	}
}
.b-cart-del{
	&__nav{
		padding-bottom: 30px;
		li{
			padding: 0 8px;
		}
	}
	&__form{
		padding: 0 0 44px;
	}
	&__row{
		padding: 0 0 30px;
	}
	&__left{
		width: auto;
		padding: 0 0 15px;
		float: none;
		&--middle {
			min-height: 10px;
			display: block;
		}
	}
	&__right{
		width: auto;
		float: none;
	}
}
.b-cart-total{
	margin: 10px 0 0;
	padding-top: 20px;
	padding-bottom: 20px;
	display: block;
	&__left{
		width: auto;
		padding: 0 0 5px;
	}
	&__right{
		width: auto;
		display: block;
		.b-btn{
			min-width: 100%;
			margin-top: 15px;
		}
	}
}
.b-cart-promo{
	padding: 10px 0 0;
	&__fild{
		width: calc(100% - 118px);
		padding-right: 10px;
	}
	&__btn{
		width: 118px;
	}
}
// news
.b-new-main{
	padding-bottom: 40px;
	&__img{
		padding-bottom: 30px;
	}
	&__head{
		padding-bottom: 20px;
		display: block;
	}
	.b-tit,
	.b-sub-tit{
		padding-bottom: 10px;
	}
	p{
		padding: 0 0 20px;
	}
}
.b-news-list{
	padding-bottom: 50px;
}
.b-new-prev{
	&__img{
		padding-bottom: 20px;
	}
}
// def page
.b-post-page{
	padding: 0 0 50px;
	&__row{
		margin: 0;
	}
	&__col{
		padding-left: 0;
		padding-right: 0;
		float: none;
		&--md{
			width: auto;
		}
	}
	h1,
	h2,
	h3,
	h4,
	h5,
	h6{
		padding: 0 0 18px;
		letter-spacing: 2px;
	}
	h1{
		font-size: 26px;
		line-height: 30px;
	}
	h2{
		font-size: 18px;
		line-height: 22px;
	}
	h3{
		font-size: 18px;
		line-height: 18px;
	}
	h4{
		font-size: 16px;
		line-height: 16px;
	}
	h5{
		font-size: 15px;
		line-height: 15px;
	}
	hr{
		margin: 15px 0 32px;
	}
	p{
		padding: 0 0 18px;
	}
	img{
		&.alignleft{
			width: 100%;
			margin-right: 0;
			float: none;
		}
		&.alignright{
			width: 100%;
			margin-left: 0;
			float: none;
		}
	}
	.b-tit,
	.b-sub-tit{
		padding: 0 0 20px;
	}
}
// delivery
.b-del-speed{
	margin: 0 0 50px;
	display: block;
	&__img{
		display: none;
	}
	&__txt{
		height: auto;
		padding-top: 30px;
		padding-bottom: 30px;
		.icon{
			margin-bottom: 15px;
		}
	}
	&__row{
		margin: 0;
	}
	&__col{
		width: auto;
		padding-left: 0;
		padding-right: 0;
		float: none;
	}	
	.b-sub-tit{
		padding-bottom: 0;
	}
}
.b-del-how{
	padding: 0 0 20px;
	margin: 0;
	display: block;
	white-space: normal;
	&__col{
		width: auto;
		max-width: 320px;
		padding: 0 0 30px;
	}
	.img{
		margin: 0 0 10px;
	}
	.b-sub-tit{
		padding-bottom: 20px;
	}
}
.b-del-area{
	padding: 0 0 50px;
	&__txt{
		width: auto;
		padding: 0 0 30px;
		float: none;
	}
	&__map{
		width: auto;
		float: none;
	}
	.b-sub-tit{
		padding-bottom: 20px;
	}
}