// modal.scss

// form.scss
.jq-number{
	&--lg{
		max-width: 72px;
	}
}
.b-checkbox,
.b-radio{
	.icon{
		margin-left: 6px;
	}
   &--ver2{
		padding-left: 10px;
   }
}
// main.scss
.b-page{
	&__nav-main{
    	width: 215px;
    }
	&__nav-cat{
		height: 80px;
		padding: 0;
    	top: auto;
    	bottom: 0;
    }
    &__footer{
    	margin-bottom: 80px;
    }
    &.is-open{
    	&:before{
    		width: calc(100% - 215px);
    	}
    }
}
// NAV MAIN
// NAV CAT
.b-cat-nav{
	width: 100%;
	border-right: none;
	border-top: 1px solid $color-bd;
	font-size: 11px;
	line-height: 13px;
	letter-spacing: 1px;
	@include flex(to middle-col);
	&:before{
    	display: none;
	}
	&__list{
		width: 100%;
		overflow: visible;
		@include flex(to center);
	}
	.icon{
		width: 100%;
		height: 20px;
	}
	a{
		padding: 46px 0 0;
		text-align: center;
	}
	&__item{
		width: 90px;
		padding: 0 10px;
		text-align: center;
	}
	&.is-hover{
		width: 100%;	
	}
}
// HEADER
.b-header{
	&__col{
		&--logo{
			width: 120px;
		}
		&--btn{
			width: calc(100% - 120px - 155px - (50% - 209px) - 209px);
		}
		&--address{
			width: 155px;
		}
		&--cart{
			width: calc(50% - 209px);
		}
		&--phone{
			width: 209px;
		}
	}
}
// FOOTER
.b-footer{
	padding-left: 20px;
}
// MAIN
.b-main{
	padding-left: 0;
}
// home page
// -- slider
.b-home-slid{
	&__tit{
		font-size: 50px;
		line-height: 58px;
	}
}
// catalog
.b-cat{
	&__head{
		&-tit{
			width: calc(100% - 111px - 190px);
		}
		&-btn{
			width: 190px;
		}
	}
}
.b-sort{
	&__item{
		margin: 0 10px;
	}
}
.b-filter{
	&__left{
		width: calc(68% - 10px);
	}
	&__right{
		width: calc(32% - 10px);
	}
	&__item{
		padding: 0 10px;
	}
}
// card
.b-card{
	&__txt{
		width: 50%;
	}
	&__img{
		width: 50%;
	}
	&__foot{
		&-col{
			&--price{
				width: calc(100% - 92px - 160px);
				font-size: 20px;
				line-height: 22px;
			}
			&--numb{
				width: 92px;
			}
			&--btn{
				width: 160px;
			}
		}
	}
}
// cart
.b-cart{
	&__left{
		padding-right: 0;
	}
	&__right{
		width: auto;
		padding-bottom: 90px;
		position: relative;
		&-sticky{
			position: relative !important;
			top: 0 !important;
		}
	}
}
.uk-sticky-placeholder{
	display: none;
}
.b-cart-del{
	&__nav{
		li{
			padding-left: 10px;
			padding-right: 10px;
		}
	}
}
.b-cart-promo{
	&__fild{
		width: calc(100% - 160px);
	}
	&__btn{
		width: 160px;
	}
}
// def page
.b-post-page{
	&__col{
		&--lg{
			padding-bottom: 20px;
		}
		&--lg,
		&--sm{
			width: auto;
			float: none;
		}
		img{
			margin-left: auto;
			margin-right: auto;
		}
	}
	&__imgs{
		margin: 0 -10px;
		&-item{
			padding: 5px 10px 15px;
		}
	}
}
// delivery
.b-del-speed{
	display: block;
	&__img{
		display: none;
	}
	&__txt{
		width: auto;
		padding-left: 20px;
		padding-right: 20px;
	}
	&__row{
		margin: 0 -10px;
	}
	&__col{
		padding-left: 10px;
		padding-right: 10px;
	}	
}
.b-del-how{
	&__col{
		width: 33%;
	}
}
.b-del-area{
	&__txt{
		padding-right: 20px;
	}
}