@mixin flex($direction) {
	display: flex;
    flex-wrap: nowrap;
	@if $direction == (to middle) {
		flex-direction: row;
		align-items: center;
	} @else if $direction == (to middle-col) {
		flex-direction: column;
		justify-content: center;
	} @else if $direction == (to space-between) {
		flex-direction: row;
		justify-content: space-between;
	} @else if $direction == (to bottom) {
		flex-direction: row;
		align-items: flex-end;
	} @else if $direction == (to bottom-col) {
		flex-direction: column;
		justify-content: flex-end;
	} @else if $direction == (to top) {
		flex-direction: row;
		align-items: flex-start;
	} @else if $direction == (to right) {
		flex-direction: row;
		justify-content: flex-end;
	} @else if $direction == (to right-col) {
		flex-direction: column;
		align-items: flex-end;
	} @else if $direction == (to center) {
		flex-direction: row;
		justify-content: center;
	} @else if $direction == (to center-col) {
	 	flex-direction: column;
		align-items: center;
	} @else if $direction == (to top) {
		flex-direction: row;
	 	justify-content: flex-start;
    	align-content: stretch;
	}
}