[class^="icon-"], 
[class*=" icon-"],
.b-icomoon {
    &:before{
      font-family: $ff-icomoon !important;
      speak: none;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }
}

