*,
*::before,
*::after {
    box-sizing: border-box;
}
html,
body {
    width: 100%;
    min-height: 100%;
    padding: 0;
    margin: 0;
    font-family: $ff-helvetica;
    font-size: 50px;
    line-height: 1.714em;
    letter-spacing: 0.071em;
    color: $color-greyD;
    position: relative;
}
html{
    margin-top: 0 !important;
}
body {
    overflow-x: hidden;
}
div,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
select,
input,
textarea,
img,
dl,
dt,
dd,
table,
div,
td,
theder,
button,
blockquote,
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
meter,
main,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr {
    padding: 0;
    margin: 0;
}
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
main,
meter,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr,
address {
    display: block;
    font-style: normal;
}
ul,
ol,
li {
    list-style: none;
}
*:active,
*:focus,
a,
input,
textarea,
button {
    border-radius: 0;
    outline: none !important;
    -moz-appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    box-shadow: none;
}
input,
textarea{
    font-family: $ff-helvetica;
}
button,
input[type="submit"]{
    font-family: $ff-helvetica;
}
textarea{
    resize: none;
}
::-moz-focus-inner{
    border: none;
}
a {
    transition: all 0.3s ease;
    color: $color-greyD;
    text-decoration: none;
    &:hover{
        color: $color-green;
    }
}
.cf {
    &:before,
    &:after {
        content: '';
        display: block;
        overflow: hidden;
    }
    &:after {
        clear: both;
    }
}
.b-flex {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    &--middle {
        align-items: center;
    }
    &--space-between {
        justify-content: space-between;
    }
    &--bottom {
        align-items: flex-end;
    }
    &--end,
    &--end-col {
        align-items: flex-end;
    }
    &--middle-col,
    &--bottom-col,
    &--space-between-col,
    &--end-col,
    &--top-col {
        flex-direction: column;
    }
    &--space-between-col {
        justify-content: space-between;
    }
    &--middle-col {
        justify-content: center;
    }
    &--bottom-col {
        justify-content: flex-end;
    }
    &--top-col {
        justify-content: flex-start;
    }
    &--right {
        justify-content: flex-end;
    }
    &--center {
        justify-content: center;
    }
}
.b-tit{
    font-size: 2.571em;
    font-weight: bold;
    line-height: 1.111em;
    letter-spacing: 0.111em;
    color: $color-body;
}
.b-sub-tit{
    font-size: 1.714em;
    font-weight: bold;
    line-height: 1.250em;
    letter-spacing: 0.167em;
    color: $color-body;
    &--sm{
        font-size: 1.286em;
        line-height: 1.222em;
        letter-spacing: 0.111em;
    }
}
.b-date{
    padding-left: 1.786em;
    display: block;
    position: relative;
    &:before{
        margin-top: -0.500em;
        display: block;
        font-size: 1.000em;
        line-height: 1.000em;
        position: absolute;
        top: 50%;
        left: 0;
        @include icomoon("\e907", $color-greyD);
        z-index: 1;
    }
}
.wow{
    visibility: hidden;
}
.popover {
    max-width: 18.286em;
    margin-top: 0.714em;
    padding: 1.286em 1.429em 1.071em;
    display: none;
    background-color: $color-greyL;
    box-shadow: 0px 0px 1.071em rgba(52, 52, 52, 0.15);
    font-size: 1.000em;
    line-height: 1.714em;
    text-align: left;
    color: $color-greyD;
    white-space: normal;
    position: absolute;
    top: 100%;
    left: 0 !important;
    z-index: 1010;
    .b-btn-close{
        position: absolute; 
        top: -0.500em;
        right: -0.500em;
    }
    .arrow {
        display: none;
    }
    &__row{
        width: 100%;
        padding: 0.750em 0;
        font-size: 1em;
        line-height: 1.500em;
        position: relative;
        @include flex(to middle);
    }
    &__img{
        width: calc(100% - 9.167em);
        padding: 0 1.667em 0 0;
        img{
            width: 100%;
            display: block;
        }
    }
    &__txt{
        width: 9.167em;
    }
}
.sticky-bottom{
    height: 10.714em;
}
// scroll 
.scroll-wrapper {
    overflow: hidden;
    position: relative;
    >.scroll-content {
        height: auto !important;
        max-height: 11.929em !important;
        overflow-y: scroll;
        position: relative;
        margin-bottom: 0px !important;
        &::-webkit-scrollbar {
            height: 0;
            width: 0;
        }
    }
    .scroll-element {
        display: none;
        position: absolute;
        z-index: 10;
        &.scroll-scrollx_visible,
        &.scroll-scrolly_visible {
            display: block;
        }
        .scroll-bar,
        .scroll-arrow {
            cursor: default;
        }
        div {
            display: block;
            height: 100%;
            left: 0;
            top: 0;
            width: 100%;
            position: absolute;
            z-index: 10;
        }
        &.scroll-y {
            right: 0;
            top: 0;
            bottom: 0;
            width: 0.571em;
            &:before{
                width: 100%;
                height: 100%;
                display: block;
                content: '';
                border: 0.071em solid $color-bd;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
            }
        }
        .scroll-element_outer {
            overflow: hidden;
        }
        .scroll-element_outer,
        .scroll-element_track,
        .scroll-bar {
            border-radius: 0px;
        }
        .scroll-bar {
            background: $color-body;
        }
        &:hover,
        &.scroll-draggable {
            .scroll-bar {
                background: $color-green;
            }
        }
    }
}