// modal.scss
.fancybox-slide--html {
    padding: 0 10px;
}
.fancybox-content{
	padding: 40px 20px 30px;
}
.b-modal{
    padding: 15px 20px 20px;
}
// main.scss
.b-page{
	&__nav-main{
    	width: 305px;
    }
    &.is-open{
    	&:before{
    		width: calc(100% - 305px);
    	}
    }
}
// NAV MAIN
.b-main-nav{
	&__item{
		padding-left: 20px;
		padding-right: 20px;
	}
}
// NAV CAT
.b-cat-nav{
	width: 116px;
	&:before{
		left: 115px;
	}
	&__item{
		padding: 10px 10px 10px 40px;
	}
	&.is-hover{
		width: 305px;
		&:before{
    		left: 305px;
    	}	
	}
}
// HEADER
.b-header{
	padding-left: 20px;
	padding-right: 20px;
	&:before{
		width: 20px;
	}
	&__col{
		&--logo{
			width: 140px;
		}
		&--btn{
			width: calc(100% - 140px - 160px - 140px - 209px);
		}
		&--address{
			width: 160px;
		}
		&--cart{
			width: 140px;
		}
		&--phone{
			width: 209px;
		}
	}
}
// FOOTER
.b-footer{
	padding-left: calc(116px + 20px);
	padding-right: 20px;
}
// MAIN
.b-main{
	padding-left: 116px;
	&__center{
		padding-left: 20px;
		padding-right: 20px;
	}
}
// home page
// -- slider
.b-home-slid{
	&__entry{
		padding: 0 20px;
	}
	&__sub-tit{
		padding-top: 15px;
		padding-bottom: 15px;
	}
	&__tit{
		padding-top: 15px;
		padding-bottom: 15px;
		font-size: 64px;
		line-height: 72px;
		letter-spacing: 1px;
	}
	&__btn{
		padding-top: 15px;
		padding-bottom: 15px;
	}
}
// --txt home
.b-txt-page{
	&__col{
		&-right{
			padding-left: 40px;
		}
	}
}
// catalog
.b-cat{
	&__slider{
		margin: 0 -10px;
		&-item{
			padding: 10px;
		}
	}
	&__list{
		margin: 0 -10px;
		&-item{
			padding: 10px;
		}
	}
}
.b-sort{
	&__item{
		margin: 0 15px;
	}
}
.b-filter{
	&__left{
		width: calc(68% - 10px);
		padding-left: 10px;
		padding-right: 10px;
	}
	&__right{
		width: calc(32% - 10px);
	}
	&__item{
		padding: 0 10px;
	}
}
// card
.b-card{
	padding-left: 20px;
	padding-right: 20px;
	&__txt{
		width: 55%;
	}
	&__img{
		width: 45%;
		padding-left: 30px;
	}
	&__add{
		@include flex(to top);
		&-check{
			width: calc(100% - 93px);
		}
		.b-checkbox{
			width: calc(50% - 10px);
			margin-bottom: 10px;
		}
	}
	&__foot{
		&-col{
			&--price{
				width: calc(100% - 132px - 180px);
			}
			&--numb{
				width: 132px;
				padding-left: 20px;
			}
			&--btn{
				width: 180px;
				padding-left: 20px;
			}
		}
	}
}
// cart
.b-cart{
	&__left{
		padding-right: 35%;
	}
	&__right{
		width: calc(35% - 20px);
	}
}
.b-cart-del{
	&__nav{
		li{
			padding-left: 15px;
			padding-right: 15px;
		}
	}
}
.b-cart-total{
	padding-left: 20px;
	padding-right: 20px;
	&__left{
		span{
			padding-left: 10px;
		}
	}
}
.b-cart-widget{
	padding-left: 20px;
	padding-right: 20px;
}
.b-news-list{
	margin: 0 -10px;
	&__item{
		padding-left: 10px;
		padding-right: 10px;
	}
}
// def page
.b-post-page{
	&__row{
		margin: 0 -10px;
	}
	&__col{
		padding-left: 10px;
		padding-right: 10px;
	}
	img{
		&.alignleft{
			margin-right: 20px;
		}
		&.alignright{
			margin-left: 20px;
		}
	}
	&__imgs{
		margin: 0 -10px;
		&-item{
			padding: 5px 10px 15px;
		}
	}
}
// delivery
.b-del-speed{
	&__txt{
		padding-left: 20px;
		padding-right: 20px;
	}
	&__row{
		margin: 0 -10px;
	}
	&__col{
		padding-left: 10px;
		padding-right: 10px;
	}	
}
.b-del-how{
	margin: 0 -10px;
	&__col{
		width: 30%;
		padding: 0 10px;
	}
	&__list{
		margin: 0 -10px;
		&-item{
			padding: 0 10px;
		}
	}
}
.b-del-area{
	&__txt{
		padding-right: 30px;
	}
}