// modal.scss
.b-modal{
    padding: 25px 30px 30px;
}
// main.scss
// NAV MAIN
.b-main-nav{
	&__item{
		padding-left: 30px;
		padding-right: 30px;
	}
}
// NAV CAT
.b-cat-nav{
	width: 126px;
	&:before{
		left: 125px;
	}
	&__item{
		padding: 10px 10px 10px 50px;
	}
	&.is-hover{
		width: 325px;
		&:before{
    		left: 325px;
    	}	
	}
}
// HEADER
.b-header{
	padding-left: 30px;
	padding-right: 30px;
	&:before{
		width: 30px;
	}
	&__col{
		&--logo{
			width: 140px;
		}
		&--btn{
			width: calc(100% - 140px - (50% - 63px));
		}
		&--address{
			width: calc((50% - 63px) - 140px - 209px);
		}
		&--cart{
			width: 140px;
		}
		&--phone{
			width: 209px;
		}
	}
}
// FOOTER
.b-footer{
	padding-left: calc(126px + 30px);
	padding-right: 30px;
}
// MAIN
.b-main{
	padding-left: 126px;
	&__center{
		padding-left: 30px;
		padding-right: 30px;
	}
}
// home page
// -- slider
.b-home-slid{
	&__entry{
		padding: 0 30px;
	}
	&__sub-tit{
		padding-top: 20px;
		padding-bottom: 20px;
	}
	&__tit{
		padding-top: 20px;
		padding-bottom: 20px;
		letter-spacing: 2px;
	}
	&__btn{
		padding-top: 20px;
		padding-bottom: 20px;
	}
}
// --txt home
.b-home-txt{
	&__col{
		&-right{
			padding-left: 50px;
		}
	}
}
// card
.b-card{
	padding-left: 30px;
	padding-right: 30px;
}
// cart
.b-cart{
	&__right{
		width: calc(38% - 30px);
	}
}
// def page
.b-post-page{
	&__row{
		margin: 0 -15px;
	}
	&__col{
		padding-left: 15px;
		padding-right: 15px;
	}
	img{
		&.alignleft{
			margin: 0 30px 30px 0;
		}
		&.alignright{
			margin: 0 0 30px 30px;
		}
	}
}
// delivery
.b-del-speed{
	&__txt{
		padding-left: 30px;
		padding-right: 30px;
	}
}
.b-del-area{
	&__txt{
		padding-right: 50px;
	}
}